<template>
	<div class="container">
		<my-header title="补传资料"></my-header>
		<section>
			<div>
				<span>问题件描述</span>
				<div>
					<cube-textarea  v-model="describe" style="height:100px;" :indicator={remain:false} :maxlength=300 placeholder="请补充**资料"></cube-textarea >
				</div>
			</div>
			<div>
				<h5>
					<span>其他补充资料</span>
					<span>已上传<strong>{{otherNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="13" field="otherNum" @addNum="addNumFun" ref="otherNum" @deleNum="deleNumFun" :max=10 :largeclass="claimApply.documentNo.otherInformation"></img-upload>
				</div>
			</div>
		</section>
		<div class="btn">
			<cube-button @click="submit" :inline="true" :light="true">确认</cube-button>
		</div>
	</div>
</template>
<script>
import Vue from 'vue';
import MyHeader from "@/components/model/MyHeader";
import ImgUpload from "@/components/pages/claimApply/components/ImgUpload";
import {createAPI,Style,Textarea,Button,Dialog} from 'cube-ui';
import {SELECTMAXNUMBER,INSERTMEND} from "@/api";
import {ISINPUTNULL} from "@/utils/publicUtils";
import {mapState} from "vuex";
Vue.use(Dialog);
export default{
	name:"SuppInformation",
	data(){
		return {
			describe:"",
			otherNum:0,
			organNo:"",
			pictureOrder:"1000",
			claimNo:this.$route.query.claimNo   //赔案号
		}
	},
	async created(){
		let datas=await SELECTMAXNUMBER({claimNo:this.claimNo});
		if(!datas.data.data){
			datas.data.data={}
		}
		if(!datas.data.success){
			this.showBtn(datas.data.message,false)
		}else{
			this.organNo=datas.data.data.accidentBranch3;
			this.pictureOrder=datas.data.data.pictureOrder;
		}
	},
	computed:{
  		...mapState(['claimApply','documentNo'])
    },
	methods:{
		deleNumFun(field){
    		this[field]--;
	    },
	  	addNumFun(num,field,max){
	    	this[field]+=num;
	    	this[field]=this[field]>max?max:this[field];
	    },
	    showBtn(title,iconFlag=true) {
	      let icon=iconFlag?'fa fa-check-circle-o supplement':'';
	      this.$createDialog({
	        type: 'confirm',
	        icon: icon,
	        title: title,
	        content: '',
	        confirmBtn: {
	          text: '确定',
	          active: true,
	          disabled: false,
	          href: 'javascript:;'
	        },
	        cancelBtn: {
	          text: '返回首页',
	          active: false,
	          disabled: false,
	          href: 'javascript:;'
	        },
	        onConfirm: () => {
	        	try{
	        		//这个可以关闭安卓系统的手机
		            document.addEventListener(
		              "WeixinJSBridgeReady",
		              function() {
		                WeixinJSBridge.call("closeWindow");
		              },
		              false
		            );
		            //这个可以关闭ios系统的手机
		            WeixinJSBridge.call("closeWindow");
	            }
	            catch(err){
	            	window.location.href=process.env.HOMEAPI
	            }
          },
	        onCancel: () => {
	        	window.location.href=process.env.HOMEAPI
	        }
	      }).show()
	    },
	    async submit(){
	    	ISINPUTNULL(this,this.describe,"问题件描述")&&ISINPUTNULL(this,this.otherNum,"其他补充资料")&&
	    	await INSERTMEND({
	    		Message:this.describe,
	    		data:this.picName([].concat(this.$refs.otherNum.files),++this.pictureOrder)
	    	}).then(()=>{
	    		--this.pictureOrder;
				this.showBtn('补传资料完成！')
	    	})
	   	},
	   	picName(arr,order='1001'){
	    	arr.forEach((item)=>{
	    		if(!item.claimNo){
	    			item.claimNo=this.claimNo;
	    		}
				item.pictureorder=order+"";
				item.name=this.organNo+"_"+this.claimNo+"_"+order+++"_"+item.status+"_"+2;
			});
	    	return arr;
    	},
	},
	components:{
	  	MyHeader,ImgUpload,
	  	CubeTextarea:Textarea,
	  	CubeButton:Button,
	}
}
</script>
<style lang="stylus" scoped>
.container{
	height:100%;
	display:flex;
	flex-direction: column;
	&>section{
		font-size:14px;
		&>div{
			padding:10px;
			background:#fff;
			margin-bottom:15px;
		}
		&>div:nth-child(1){
			&>span{
				line-height:40px;
			}
		}
		&>div:nth-child(2){
			&>h5{
					height:40px;
					line-height:50px
					display:flex;
					justify-content: space-between;
					&>span:nth-child(2){
						font-size:16px;
						color:#ccc;
						&>strong{
							color:#fd7f47;
							margin:0 5px;
						}
					}
				}
		}
	}
	&>.btn{
		text-align:center;
		margin-top:60px;
		.cube-btn-light{
			background:#fd7f47;
			color:#fff;
			font-size:15px;
			padding:14px 56px;
		}
	}
}
</style>
<style>
.cube-dialog-title .cube-dialog-title-def{
    white-space: normal;
    line-height:30px
}
.cube-dialog-icon .supplement{
	padding:0;
	font-size:70px;
	color:#e98f36;
	background:none;
	width: 100%;
}
</style>



